@import url("https://fonts.googleapis.com/css2?family=Inter:wght@400;600;700&display=swap");

.Header {
  position: fixed; // Fixes the header to the viewport
  top: 0; // Aligns the header to the top of the viewport
  left: 0; // Aligns the header to the left edge
  right: 0; // Extends the header to the right edge
  // background-color: white;  // Ensures the header has a background
  // from analogue pocket
  --tw-bg-opacity: 1;
  background-color: rgb(13 14 16 / var(--tw-bg-opacity));
  color: white;
  z-index: 1000; // Keeps the header above other content
  display: flex;
  // display: flex;
  justify-content: space-between;
  padding: 0 10px;
  width: 100%;
  // font-family: 'Verdana', monospace, sans-serif;
  // font-family: 'Brush Script MT', monospace;
  font-family: "Inter", sans-serif;

  ul {
    display: flex;
    list-style-type: none;
    align-items: center;
    // gap: 20px;
    // gap: 22%;
    // margin: 0 10%;
    justify-content: space-around;
    padding-right: 10%;
    width: 70%;
    padding-left: 10%;
  }

  .SocialNetwork {
    // width: 20%;
    gap: 20px;
    margin-right: 20px;
    display: flex;
    align-items: center;
    justify-content: flex-end;
    size: 10px;
  }

  h1 {
    font-weight: 600;
  }

  .link {
    color: white;
    text-decoration: none;
    padding: 5px 10px 5px 10px;
    // font-weight: 600;
    &:hover {
      // background-color: blue;
      text-decoration: underline;
      // border-radius: 1em;
      color: white;
    }
  }

  .logo-link {
    color: white;
    text-decoration: none;
    // padding: 5px 10px 5px 20px;
    padding: 1% 2% 1% 100%;
    font-weight: bolder;
    font-size: larger;
  }
}
