.introduction {
    &-container {
        display: flex;
        width: 100%;
        height: 100vh;
        justify-content: center;
    }

    &-content {
        margin-top: 10%;
        display: flex;
        flex-direction: column;
        gap: 20px;
        color: rgb(179, 179, 179);
        width: 70%;

        h1 {
            font-size: 4.5vw;
            color: white;
            text-align: center;
        }

        p {
            // color: rgb(138, 138, 138);
            font-size: clamp(18px,1.837vw,100px);
            margin: 0% 5%;
            text-align: center;
        }
        @media (max-width: 1024px) {
            margin-top: 20%;
        }
    }
}

.contact-container {
    margin-bottom: 10vh;
}